import { LoadingProvider } from './../../providers/loading/loading';
import { AuthProvider } from './../../providers/auth/auth';
import { RestProvider } from './../../providers/rest/rest';
import { Component } from '@angular/core';
import { NavController, NavParams, ViewController, IonicPage, InfiniteScroll, ModalController } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';

/**
 * Generated class for the CartPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-cart-history',
  templateUrl: 'cart-history.html',
})
export class CartHistoryPage {

  items: any;
  nextSelector: string;
  page: number;
  perPage: number;
  totalData: number;
  totalPage: number;
  

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public view: ViewController,
    public rest: RestProvider,
    public auth: AuthProvider,
    public modal: ModalController,
    public loading: LoadingProvider,
    public global: GlobalProvider
  ) {
    this.page = 1;
    this.perPage = 0;
    this.totalData = 0;
    this.totalPage = 0;
    this.items = [];
    this.rest.getOrderHistory().then(
      (scs: any) => {
        for (let i = 0; i < scs.order.data.length; i++) {
          this.items.push( scs.order.data[i] );
        }
        this.nextSelector = scs.order.next_page_url;
        this.page = scs.order.current_page;
        this.totalData = scs.order.total;
        this.totalPage = scs.order.last_page;
      },
      (err) => {
        console.log(err);
        
      }
    );  
  }

  ionViewWillEnter(){
    if (!this.auth.authenticated()) {
      this.auth.logout();
      this.navCtrl.setRoot('LoginPage');
      return;
    }
  }

  showTransactionDetail(itemId) {
    this.loading.show()
    this.rest.getOrderHistoryDetail(itemId).then(
      (scs: any) => {
        this.loading.hide();
        let transaction = this.modal.create('CartHistoryDetailPage', {order: scs.order, details: scs.details});
        transaction.present();
      },
      (err) => {
        console.log(err);
        
      }
    );
    
  }

  openCartPage() {
    this.navCtrl.push('CartPage');
  }

  doInfinite(infiniteScroll: InfiniteScroll) {
    this.rest.getOrderHistory(this.nextSelector).then(
      (scs: any) => {
        for (let i = 0; i < scs.order.data.length; i++) {
          this.items.push( scs.order.data[i] );
        }
        this.nextSelector = scs.order.next_page_url;
        this.page = scs.order.current_page;
        this.totalData = scs.order.total;
        this.totalPage = scs.order.last_page;

        infiniteScroll.complete();

        if (this.items.length > 90) {
          infiniteScroll.enable(false);
        }
      },
      (err) => {
        console.log(err);
        
      }
    );
  }

  presentNotifications(myEvent) {
    // let popover = this.popoverCtrl.create('NotificationPage');
    // popover.present({
    //   ev: myEvent
    // });
    this.navCtrl.push('NotificationPage');
  }

}



